<template>
    <div>
        <TopNav title="编辑抢购关联的商品"></TopNav>
        <div class="wyu-main">
            <div class="wyu-block" v-loading="loading">
                <div class="wyu-tips wyu-tips-info" style="margin-bottom:10px;">
                    <p class="tit">1)抢购产品包邮以 关联产品为准</p>
                </div>
                <el-form :model="goods"  ref="goodsInfo" size="mini" label-width="100px">
                    <div class="wyu-title">
                        <h2>选择产品</h2>
                    </div>
                    <el-form-item label="选择产品" >
                        <product-select-spec @selGoods="getGoods" :prom_type='0'></product-select-spec>
                    </el-form-item>

                    <el-form-item label="PLUS会员特价">
                        <el-switch v-model="goods.is_vip" :active-value="1" :inactive-value="0"></el-switch>
                    </el-form-item>

                    <el-form-item label="产品名称 " v-if="goods">
                        {{goods.goods_name}}
                    </el-form-item>
                    <el-form-item label="产品图片" v-if="goods">
                        <img :src="goods.photo" width="100"/>
                    </el-form-item>
                    <el-form-item label="选择规格"  v-if="goods">
                        <div v-if="goods.sku_type == 0">
                            价格：{{goods.shop_price}}
                        </div>
                        <div v-if="goods.sku_type == 1">
                            <el-table :data="goodsItem" size="mini" row-key="spec_key" @selection-change="seleSpec" ref="multipleTable" height="350"  border style="width: 100%">
                                <el-table-column :reserve-selection="true" prop="spec_key" type="selection" width="55"></el-table-column>

                                <el-table-column prop="key_name" label="规格项"></el-table-column>

                                <el-table-column prop="shop_price" label="价格" width="100"></el-table-column>

                                <el-table-column label="抢购价" width="150">
                                    <template slot-scope="scope">
                                        <el-input v-model="goodsItem[scope.$index].rushbuy_price"></el-input>
                                    </template>
                                </el-table-column>

                                <el-table-column v-if="goods.is_vip == 1" label="PLUS会员抢购价" width="150" >
                                    <template slot-scope="scope">
                                        <el-input v-model="goodsItem[scope.$index].vip_rushbuy_price"></el-input>
                                    </template>
                                </el-table-column>

                                <el-table-column label="数量" width="100">
                                    <template slot-scope="scope">
                                        <el-input v-model="goodsItem[scope.$index].goods_num"></el-input>
                                    </template>
                                </el-table-column>

                                <!--<el-table-column prop="surplus_num" label="剩余数量" width="100"></el-table-column>-->
                            </el-table>
                        </div>

                    </el-form-item>

                    <el-form-item label="抢购价" v-if="goods.sku_type == 0">
                        <el-input v-model="goods.rushbuy_price" style="width: 500px;"></el-input>
                        <p class="wyu-form-tips">抢购价格</p>
                    </el-form-item>

                    <el-form-item label="会员抢购价" v-if="goods.sku_type == 0 && goods.is_vip == 1">
                        <el-input v-model="goods.vip_rushbuy_price" style="width: 500px;"></el-input>
                        <p class="wyu-form-tips">PLUS会员抢购价格</p>
                    </el-form-item>

                    <el-form-item label="总数量" v-if="goods.sku_type == 0">
                        <el-input v-model="goods.goods_num" style="width: 500px;"></el-input>
                    </el-form-item>

                    <!--<el-form-item label="总剩余数量" >-->
                        <!--<el-input v-model="goods.surplus_num" style="width: 500px;"></el-input>-->
                    <!--</el-form-item>-->

                    <el-form-item label="排序">
                        <el-input v-model="goods.orderby" style="width: 500px;"></el-input>
                    </el-form-item>

                    <el-form-item label="是否开启">
                        <el-switch v-model="goods.is_online" :active-value="1" :inactive-value="0"></el-switch>
                    </el-form-item>
                </el-form>
            </div>
        </div>
        <div class="wyu-btn-div">
            <el-button size="small" type="primary"  @click="editGoods()">编辑抢购</el-button>
            <router-link to="/marketing/rushbuy/index" style="margin-left: 10px">
                <el-button size="small">取消</el-button>
            </router-link>
        </div>
    </div>
</template>

<script>
    import TopNav from "@/components/TopNav";
    import * as api from "@/api";
    import ProductSelectSpec from "@/pages/product/components/ProductSelectSpec";

    export default {
        name: 'GroupBuyEdit',
        components: {ProductSelectSpec, TopNav},

        data() {
            return {
                loading: false,
                active: 1,
                typeId: 0,
                list: [],
                goodsItem:[],
                goods:{
                    spec: '',
                    is_vip: 0,
                    goods_spec: {
                        itemPrice: {},
                    },
                },

                goodsDetail: {

                },
            }
        },
        created() {
            this.getDetail();

        },
        updated(){
            if(this.$refs.multipleTable){
                this.setSpecCheck();
            }
        },
        methods: {
            getGoods(val) {
                this.goods = val;
                this.goodsItem = val.goods_spec;
                if(this.$refs.multipleTable != undefined) {
                    console.log(this.$refs.multipleTable);
                    this.$refs.multipleTable.clearSelection();
                }
            },

            seleSpec(val){
                let spec_key = [];
                val.forEach( v => {
                    spec_key.push(v);
                });
                this.goods.spec = spec_key;
            },

            setSpecCheck(){
                let _spec = this.goods.goods_spec;
                let _sepc = [];
                for (let i  in _spec) {
                    _sepc.push(_spec[i]['spec_key']);
                }
                this.goodsItem.forEach(v => {
                    if(_sepc.indexOf(v.spec_key) > -1){
                        this.$refs.multipleTable.toggleRowSelection(v);
                    }
                });
            },

            getDetail() {
                this.loading = true;
                api.post("/shop/admin.marketing.rushbuy/goodsdetail", {
                    appToken: this.$store.getters.appToken,
                    rushbuy_goods_id:this.$route.params.rushbuy_goods_id,
                    rushbuy_id: this.$route.params.id,
                }).then(res => {
                    this.loading = false;
                    res.rep.forEach(val => {
                        let isHave =  res.goods.goods_spec.find((v) => {
                            return v.spec_key ==  val.spec_key;
                        });
                        val.rushbuy_price = 0;
                        val.vip_rushbuy_price = 0;
                        if(isHave){
                            val.rushbuy_price = isHave.rushbuy_price;
                            val.vip_rushbuy_price = isHave.vip_rushbuy_price;
                            val.goods_num = isHave.goods_num;
                        }
                    });
                    this.goodsItem = res.rep;
                    this.goods = res.goods;
                }).catch(() => {
                    this.loading = false;
                });
            },


            //提交商品到商品库
            editGoods() {
                this.loading = true;
                api.post("/shop/admin.marketing.rushbuy/goodsedit", {
                    appToken: this.$store.getters.appToken,
                    rushbuy_goods_id: this.$route.params.rushbuy_goods_id,
                    rushbuy_id: this.$route.params.id,
                    goods_id:this.goods.goods_id,
                    spec: JSON.stringify(this.goods.spec),
                    rushbuy_price: this.goods.rushbuy_price,
                    vip_rushbuy_price: this.goods.vip_rushbuy_price,
                    goods_num: this.goods.goods_num,
                    //surplus_num: this.goods.surplus_num,
                    orderby: this.goods.orderby,
                    is_online: this.goods.is_online,
                    is_vip: this.goods.is_vip,
                }).then((res) => {
                    this.loading = false;
                    this.$message({
                        message: '提交成功',
                        type: 'success'
                    });
                    setTimeout(() => {
                        let router = '/marketing/rushbuy/goods/' + this.$route.params.id;
                        this.$router.push(router);
                    }, 1000);
                }).catch(() => {
                    this.loading = false;
                });
            },
        }
    }
</script>
